"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import styles from "./DashboardLayout.module.css";
import ProfileHeader from "./components/ProfileHeader/ProfileHeader";
import { getUserData } from "@/lib/auth";

export default function DashboardLayout({ children }) {
  const pathname = usePathname();
  const [hover, setHover] = useState(false);
  const [openMenu, setOpenMenu] = useState(null);
  const [role, setRole] = useState("user");

  useEffect(() => {
    const user = getUserData();
    if (user?.type === "org") setRole("org");
  }, []);

  const toggleMenu = (menu) =>
    setOpenMenu(openMenu === menu ? null : menu);

  const Arrow = ({ open }) => (
    <span className={styles.arrow}>{open ? "▲" : "▼"}</span>
  );

  return (
    <div className={styles.container}>
      {/* SIDEBAR */}
      <aside
        className={`${styles.sidebar} ${hover ? styles.expand : ""}`}
        onMouseEnter={() => setHover(true)}
        onMouseLeave={() => setHover(false)}
      >
        {/* PROFILE */}
        <div
          className={styles.menu}
          onClick={() => toggleMenu("profile")}
        >
          <img src="/images/User.png" />
          {hover && (
            <div className={styles.menuText}>
              Profile <Arrow open={openMenu === "profile"} />
            </div>
          )}
        </div>

        {hover && openMenu === "profile" && (
          <div className={styles.dropdown}>
            <Link href="/dashboard/profile">My Profile</Link>
            {role === "org" && (
              <Link href="/dashboard/profile/manage">Manage</Link>
            )}
            <Link href="/dashboard/profile/delete">Delete</Link>
          </div>
        )}

        {/* ACTIVITIES */}
        <div
          className={styles.menu}
          onClick={() => toggleMenu("activities")}
        >
          <img src="/images/myactivityes.png" />
          {hover && (
            <div className={styles.menuText}>
              Activities <Arrow open={openMenu === "activities"} />
            </div>
          )}
        </div>

        {hover && openMenu === "activities" && (
          <div className={styles.dropdown}>
            <Link href="/dashboard/activities/saved">Saved</Link>
            {role === "org" && (
              <Link href="/dashboard/activities/booking">
                Booking
              </Link>
            )}
          </div>
        )}

        {/* ORGANIZER SPACE */}
        {role === "org" && (
          <>
            <div
              className={styles.menu}
              onClick={() => toggleMenu("space")}
            >
              <img src="/images/myspace.png" />
              {hover && (
                <div className={styles.menuText}>
                  My Space <Arrow open={openMenu === "space"} />
                </div>
              )}
            </div>

            {hover && openMenu === "space" && (
              <div className={styles.dropdown}>
                <Link href="/dashboard/space/create-event">
                  Create Event
                </Link>
                <Link href="/dashboard/space/my-event">
                  My Events
                </Link>
              </div>
            )}
          </>
        )}

        {/* SETTINGS */}
        <div
          className={styles.menu}
          onClick={() => toggleMenu("settings")}
        >
          <img src="/images/Settings.png" />
          {hover && (
            <div className={styles.menuText}>
              Settings <Arrow open={openMenu === "settings"} />
            </div>
          )}
        </div>

        {hover && openMenu === "settings" && (
          <div className={styles.dropdown}>
            <Link href="/dashboard/settings/notification">
              Notification
            </Link>
            <Link href="/dashboard/settings/email">
              Email
            </Link>
          </div>
        )}
      </aside>

      {/* CONTENT */}
      <main className={styles.content}>
        <ProfileHeader role={role} />
        <div className={styles.page}>{children}</div>
      </main>
    </div>
  );
}
